@charset "utf-8";



// Our variables
$base-font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
$base-font-size:   16px;
$base-font-weight: 400;
$small-font-size:  $base-font-size * 0.875;
$base-line-height: 1.5;

$spacing-unit:     30px;

$text-color:       #111;
$background-color: #fdfdfd;
$brand-color:      #2a7ae2;

$grey-color:       #828282;
$grey-color-light: lighten($grey-color, 40%);
$grey-color-dark:  darken($grey-color, 25%);

// Width of the content area
$content-width:    800px;

$on-palm:          600px;
$on-laptop:        800px;



// Use media queries like this:
// @include media-query($on-palm) {
//     .wrapper {
//         padding-right: $spacing-unit / 2;
//         padding-left: $spacing-unit / 2;
//     }
// }
@mixin media-query($device) {
    @media screen and (max-width: $device) {
        @content;
    }
}



// Import partials from `sass_dir` (defaults to `_sass`)
@import
        "base",
        "layout",
        "syntax-highlighting"
;

/**
 * Reset some basic elements
 */
 body, h1, h2, h3, h4, h5, h6,
 p, blockquote, pre, hr,
 dl, dd, ol, ul, figure {
   margin: 0;
   padding: 0; }
 
 /**
  * Basic styling
  */
 body {
   font: 400 16px/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
   color: #111;
   background-color: #fdfdfd;
   -webkit-text-size-adjust: 100%;
   -webkit-font-feature-settings: "kern" 1;
   -moz-font-feature-settings: "kern" 1;
   -o-font-feature-settings: "kern" 1;
   font-feature-settings: "kern" 1;
   font-kerning: normal;
   display: flex;
   min-height: 100vh;
   flex-direction: column; }
 
 @media (prefers-color-scheme: dark) {
   body {
     font: 400 16px/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
     color: #c8e1ff;
     background-color: #1b1f23;
     -webkit-text-size-adjust: 100%;
     -webkit-font-feature-settings: "kern" 1;
     -moz-font-feature-settings: "kern" 1;
     -o-font-feature-settings: "kern" 1;
     font-feature-settings: "kern" 1;
     font-kerning: normal;
     display: flex;
     min-height: 100vh;
     flex-direction: column; } }
 /**
  * Set `margin-bottom` to maintain vertical rhythm
  */
 h1, h2, h3, h4, h5, h6,
 p, blockquote, pre,
 ul, ol, dl, figure,
 .highlight {
   margin-bottom: 15px; }
 
 /**
  * `main` element
  */
 main {
   display: block;
   /* Default value of `display` of `main` element is 'inline' in IE 11. */ }
 
 /**
  * Images
  */
 img {
   max-width: 100%;
   vertical-align: middle; }
 
 /**
  * Figures
  */
 figure > img {
   display: block; }
 
 figcaption {
   font-size: 14px; }
 
 /**
  * Lists
  */
 ul, ol {
   margin-left: 30px; }
 
 li > ul,
 li > ol {
   margin-bottom: 0; }
 
 /**
  * Headings
  */
 h1, h2, h3, h4, h5, h6 {
   font-weight: 400; }
 
 /**
  * Links
  */
 a {
   color: #2a7ae2;
   text-decoration: none; }
   a:visited {
     color: #1756a9; }
   a:hover {
     color: #111;
     text-decoration: underline; }
   .social-media-list a:hover {
     text-decoration: none; }
     .social-media-list a:hover .username {
       text-decoration: underline; }
 
 @media (prefers-color-scheme: dark) {
   a {
     color: #2a7ae2;
     text-decoration: none; }
     a:visited {
       color: #1756a9; }
     a:hover {
       color: #c8e1ff;
       text-decoration: underline; }
     .social-media-list a:hover {
       text-decoration: none; }
       .social-media-list a:hover .username {
         text-decoration: underline; } }
 /**
  * Blockquotes
  */
 blockquote {
   color: #828282;
   border-left: 4px solid #e8e8e8;
   padding-left: 15px;
   font-size: 18px;
   letter-spacing: -1px;
   font-style: italic; }
   blockquote > :last-child {
     margin-bottom: 0; }
 
 @media (prefers-color-scheme: dark) {
   blockquote {
     color: #79b8ff;
     border-left: 4px solid #e8e8e8;
     padding-left: 15px;
     font-size: 18px;
     letter-spacing: -1px;
     font-style: italic; }
     blockquote > :last-child {
       margin-bottom: 0; } }
 /**
  * Code formatting
  */
 pre,
 code {
   font-size: 15px;
   border: 1px solid #e8e8e8;
   border-radius: 3px;
   background-color: #eef; }
 
 @media (prefers-color-scheme: dark) {
   pre,
   code {
     font-size: 15px;
     border: 1px solid #032f62;
     border-radius: 3px;
     background-color: #00003d; } }
 code {
   padding: 1px 5px; }
 
 pre {
   padding: 8px 12px;
   overflow-x: auto; }
   pre > code {
     border: 0;
     padding-right: 0;
     padding-left: 0; }
 
 /**
  * Wrapper
  */
 .wrapper {
   max-width: -webkit-calc(800px - (30px));
   max-width: calc(800px - (30px));
   margin-right: auto;
   margin-left: auto;
   padding-right: 15px;
   padding-left: 15px; }
   @media screen and (min-width: 800px) {
     .wrapper {
       max-width: -webkit-calc(800px - (30px * 2));
       max-width: calc(800px - (30px * 2));
       padding-right: 30px;
       padding-left: 30px; } }
 
 /**
  * Clearfix
  */
 .wrapper:after, .footer-col-wrapper:after {
   content: "";
   display: table;
   clear: both; }
 
 /**
  * Icons
  */
 .orange {
   color: #f66a0a; }
 
 .grey {
   color: #828282; }
 
 .svg-icon {
   width: 16px;
   height: 16px;
   display: inline-block;
   fill: currentColor;
   padding: 5px 3px 2px 5px;
   vertical-align: text-bottom; }
 
 /**
  * Tables
  */
 table {
   margin-bottom: 30px;
   width: 100%;
   text-align: left;
   color: #3f3f3f;
   border-collapse: collapse;
   border: 1px solid #e8e8e8; }
   table tr:nth-child(even) {
     background-color: #f7f7f7; }
   table th, table td {
     padding: 10px 15px; }
   table th {
     background-color: #f0f0f0;
     border: 1px solid #dedede;
     border-bottom-color: #c9c9c9; }
   table td {
     border: 1px solid #e8e8e8; }
 
 @media (prefers-color-scheme: dark) {
   table {
     margin-bottom: 30px;
     width: 100%;
     text-align: left;
     color: #c8e1ff;
     border-collapse: collapse;
     border: 1px solid #00003d; }
     table tr:nth-child(even) {
       background-color: #00003d; }
     table th, table td {
       padding: 10px 15px; }
     table th {
       background-color: #00003d;
       border: 1px solid #00003d;
       border-bottom-color: #00003d; }
     table td {
       border: 1px solid #00003d; } }
 /**
  * Site header
  */
 .site-header {
   border-top: 5px solid #424242;
   border-bottom: 1px solid #e8e8e8;
   min-height: 55.95px;
   line-height: 54px;
   position: relative; }
 
 @media (prefers-color-scheme: dark) {
   .site-header {
     border-top: 5px solid #424242;
     border-bottom: 1px solid #e8e8e8;
     min-height: 55.95px;
     line-height: 54px;
     position: relative;
     background-color: #24292e;
     border-bottom: unset; } }
 .site-title {
   font-size: 26px;
   font-weight: 300;
   letter-spacing: -1px;
   margin-bottom: 0;
   float: left; }
   @media screen and (max-width: 600px) {
     .site-title {
       padding-right: 45px; } }
   .site-title, .site-title:visited {
     color: #424242; }
 
 @media (prefers-color-scheme: dark) {
   .site-title {
     font-size: 26px;
     font-weight: 300;
     letter-spacing: -1px;
     margin-bottom: 0;
     float: left; } }
   @media screen and (prefers-color-scheme: dark) and (max-width: 600px) {
     .site-title {
       padding-right: 45px; } }
 @media (prefers-color-scheme: dark) {
     .site-title, .site-title:visited {
       color: #c8e1ff; } }
 .site-nav {
   position: absolute;
   top: 9px;
   right: 15px;
   background-color: #fdfdfd;
   border: 1px solid #e8e8e8;
   border-radius: 5px;
   text-align: right; }
   @media (prefers-color-scheme: dark) {
     .site-nav {
       background-color: #1b1f23; } }
   .site-nav .nav-trigger {
     display: none; }
   .site-nav .menu-icon {
     float: right;
     width: 36px;
     height: 26px;
     line-height: 0;
     padding-top: 10px;
     text-align: center; }
     .site-nav .menu-icon > svg path {
       fill: #424242; }
   .site-nav label[for="nav-trigger"] {
     display: block;
     float: right;
     width: 36px;
     height: 36px;
     z-index: 2;
     cursor: pointer; }
   .site-nav input ~ .trigger {
     clear: both;
     display: none; }
   .site-nav input:checked ~ .trigger {
     display: block;
     padding-bottom: 5px; }
   .site-nav .page-link {
     color: #111;
     line-height: 1.5;
     display: block;
     padding: 5px 10px;
     margin-left: 20px; }
     .site-nav .page-link:not(:last-child) {
       margin-right: 0; }
   @media (prefers-color-scheme: dark) {
     .site-nav .page-link {
       color: #c8e1ff;
       line-height: 1.5;
       display: block;
       padding: 5px 10px;
       margin-left: 20px; }
       .site-nav .page-link:not(:last-child) {
         margin-right: 0; } }
   @media screen and (min-width: 600px) {
     .site-nav {
       position: static;
       float: right;
       border: none;
       background-color: inherit; }
       .site-nav label[for="nav-trigger"] {
         display: none; }
       .site-nav .menu-icon {
         display: none; }
       .site-nav input ~ .trigger {
         display: block; }
       .site-nav .page-link {
         display: inline;
         padding: 0;
         margin-left: auto; }
         .site-nav .page-link:not(:last-child) {
           margin-right: 20px; } }
 
 /**
  * Site footer
  */
 .site-footer {
   border-top: 1px solid #e8e8e8;
   padding: 30px 0; }
 
 @media (prefers-color-scheme: dark) {
   .site-footer {
     border-top: 1px solid #e8e8e8;
